
export default {
    name: '',
    data() {
        return {
            file: null,
            urlVideo: ''
        }
    },
    components: {

    },
    props: {
        uidCurso: String,
        uidUnidad: String,
        uidClase: String
    },
    methods: {
        launchFilePicker(){
            this.$refs.file.click()
        },
        onFileChange(fieldName, files) {
            this.file = files[0]

            this.urlVideo = URL.createObjectURL(this.file)
        },
        sendVideo () {
            this.$emit('sendVideo', {
                uidCurso: this.uidCurso,
                uidUnidad: this.uidUnidad,
                uidClase: this.uidClase,
                file: this.file,
                urlVideo: this.urlVideo,
            })
        }
    }
}
